.catalog-search{
    position: relative;
    input[type='text']{
        background: #F7FAFA;
        border: 0.5px solid #b8bfbf;
        border-radius: 2px;
        min-height: 50px;
        min-width: 655px;
        padding: 12px 12px 12px 50px;
        transition: 0.3s;
        &:hover,
        &:focus{
            background-color: #fff;
        }
    }
    input::placeholder{
        transition: .3s;
    }
    input:focus::placeholder{
        color: #d2d2d2bf;
    }
    input[type='submit']{
        font-size: 0;
        position: absolute;
        left: 12px;
        top: 9px;
        width: 30px;
        height: 30px;
        background-size: 16px;
        border: 0;
        box-shadow: none;
        background-position: center;
        background-color: transparent;
        transition: 0.3s;
        cursor: pointer;
        &:hover{
            opacity: 0.7;
        }
    }
}